<template>
<div id="category">
<van-nav-bar
  title="预约列表"
  right-text="按钮"
  @click-right="onClickRight"
  class="nav"
/>
<van-tabs type="card" color="#516df5" :style="{margin:'20px 0'}" @click="todayClick">
	<van-tab title="今日" class="van-box" >
		<book-content :day-booking="bookingObj['todayBooking']" @onLoad="onLoad"/>
   </van-tab>
   <van-tab title="明日" class="van-box">
		<book-content :day-booking="bookingObj['tomBooking']" @onLoad="onLoad"/>
   </van-tab>
   <van-tab title="后日" class="van-box">
		<book-content :day-booking="bookingObj['afterTomBooking']" @onLoad="onLoad"/>
   </van-tab>
</van-tabs>

</div>
	
</template>

<script>
import { NavBar,Tabs,Tab,List,Cell  } from 'vant';		//这个可以封装
import {getBooking} from 'network/booking'
import BookContent from './childComps/BookContent'
export default {
  name: 'Category',
	components:{
		BookContent,
		[NavBar.name]:NavBar,
		[Tabs.name]:Tabs,
		[Tab.name]:Tab,
		[List.name]:List,
		[Cell.name]:Cell,
	},
	data() {
		return {
			bookingObj:{
				//今天
				todayBooking:{
					page:0,
					list:[],
					loading: false,
					refreshing:false,
					finished: false,
					total:0,				//同类型数据一共有多少条
					time:0,
				},
				//明天
				tomBooking:{
					page:0,
					list:[],
					loading: false,
					refreshing:false,
					finished: false,
					total:0,				//同类型数据一共有多少条
					time:20,
				},
				//后天
				afterTomBooking:{
					page:0,
					list:[],
					loading: false,
					refreshing:false,
					finished: false,
					total:11,				//同类型数据一共有多少条-假设11条
					time:23,
				},
			},
			list: [],
			loading: false,
			finished: false,
			//当前选中的：todayBooking-tomBooking-afterTomBooking
			currentDay:'todayBooking',
			dateSize:10,					//每次取多少条
		};
	},
	methods: {
		onLoad() {
			//加载当前界面
			this.getBooking(this.currentDay,this.bookingObj[this.currentDay]['time'])
		},
		onClickRight(){
			this.$Toast('按钮')
		},
		//获取数据
		getBooking(type,time){
			let objType=this.bookingObj[type]
			let page=objType.page+1
			getBooking(page,time).then(res=>{
				//先取消加载
				objType.loading = false;
				
				//判断数据是否已经没有了
				if(res.length==0){
					objType.finished = true;
					return 
				}
				let total=res[0].total
				//保存数据库一共有多少条数据
				objType.total=total
				//把得到的数据传进去
				objType.list.push(...res)
				//页码+1
				objType.page+=1
				
				//如果数组的长度大于等于数据库总条数就显示没有更多了
				if (objType.list.length >=total) {
					//没有更多了
					objType.finished = true;
				}
			})
		},
		todayClick(index){
			//当前选中的界面
			switch(index){
				case 0:
					this.currentDay='todayBooking'
					break
				case 1:
					this.currentDay='tomBooking'
					break
				case 2:
					this.currentDay='afterTomBooking'
					break
			}
		}
	},
	computed:{
		currentDayData(){
			return this.bookingObj[this.currentDay].list
		}
	},
	mounted(){
	},
	created(){
			//this.getBooking(this.currentDay)
	}
}
</script>

