<template>
	<van-list
		v-model="dayBooking['loading']"
		:finished="dayBooking['finished']"
		finished-text="没有更多了"
		@load="onLoad"
		:offset="10"
	>
		<div class="user-item-box">
			<book-user-item v-for="(item,index) in dayBooking.list" :user="item" :key="index" @agree="agree" @cancel="cancel"/>
		</div>
	</van-list>
</template>

<script>
import { List} from 'vant';		//这个可以封装
import {updateStatus} from 'network/booking'
import BookUserItem from './BookUserItem'
export default {
  name: 'BookContent',
	components:{
		BookUserItem,
		[List.name]:List,

	},
	props:{
		dayBooking:{
			type:Object,
			default(){
				return {}
			}
		}
	},
	data() {
		return {

		};
	},
	methods: {
		onLoad() {
			this.$emit('onLoad')
		},
		//确定同意
		agree(id){
			let info={
				id:id,
				status:1,
				toastText:'该项已通过',
				toastText2:'已同意',
			}
			this.changeStatus(info)
		},
		cancel(id){
			let info={
				id:id,
				status:0,
				toastText:'该项没有通过',
				toastText2:'已拒绝',
			}
			this.changeStatus(info)
		},
		//修改状态
		updateStatus(id,status){
			updateStatus(id,status).then(res=>{
				console.log(res)
			})
		},
		//修改状态-
		changeStatus(info){
			let index=this.dayBooking.list.findIndex(obj=>{return obj.id==info.id})
			
			if(this.dayBooking.list[index].status==info.status){
				this.$Toast(info.toastText)
				return
			}
			this.dayBooking.list[index].status=info.status
			//这里修改该状态
			this.updateStatus(info.id,info.status)
			this.$Toast(info.toastText2)
		}
	},
	computed:{

	},
}
</script>

