<template>
<div class="book-user-item">
		<div class="user-top-box">
			<img src="~assets/images/avatar/usericon.svg" class="uimg"/>
			<div class="user-info">
				<h3>{{user.username}}
					<div class="user-status">
						<img src="~assets/images/common/pass.svg" class="statusimg" v-if="user.status==1">
						<img src="~assets/images/common/false.svg" class="statusimg" v-else>
						
						<font :color="statusText.color">{{statusText.text}}</font>
					</div>
				</h3>
				<div class="user-info-row">电话：{{user.telephone}}</div>
				<div class="user-info-row">预约时间：{{user.booktime}}</div>
				<div class="user-info-row">地址：{{user.address}}</div>
				<div class="user-info-row">预约机器(编号)：{{user.machinename}} ({{user.machinenum}})</div>
			</div>
		</div>
		<div class="user-bottom-box">
			<van-row>
				<van-col span="4"></van-col>
				<van-col span="8"><van-button type="primary" size="normal" @click="agree" class="bnt" color="#21cbb0">同意</van-button></van-col>
				<van-col span="8"><van-button plain type="primary" color="#fe7450" class="bnt"	@click="cancel">拒绝</van-button></van-col>
				<van-col span="4"></van-col>
			</van-row>
		</div>
</div>
	
</template>

<script>
import {Col, Row,Button} from 'vant'

export default {
  name: 'BookUserItem',
  components:{
	[Col.name]:Col,
	[Row.name]:Row,
	[Button.name]:Button,
  },
  props:{
	user:{
		type:Object,
		default(){
			return {}
		}
	},
  },
  computed:{
	statusText(){
		let status={}
		status.text=this.user.status==0?'未通过':'已通过'
		status.color=this.user.status==0?'#fe7450':'green'
		return status
	}
  },
  methods:{
	agree(){
		//发送事件回上一级处理
		this.$emit('agree',this.user.id)
	},
	cancel(){
		//发送事件回上一级处理
		this.$emit('cancel',this.user.id)
	},
  }
  
}
</script>

